<template>
  <div
    v-if="referer"
    class="referer-detail"
  >
    <div class="referer-box">
      <clz-header
        :clz="referer"
        class="lm-1px-b"
      />
      <clz-content
        :clz="referer"
        class="lm-1px-b"
      />
      <detail-tab :clz="referer" />
    </div>
  </div>
</template>

<script>
import ClzHeader from "@/views/classroom/detail/components/clz-header";
import ClzContent from "@/views/classroom/detail/components/clz-content";
import DetailTab from "./detail-tab";
export default {
  components: { ClzHeader, ClzContent, DetailTab },
  data() {
    return {
      referer: undefined
    };
  },
  created() {
    this.referer = this.$route.params.item;
    if (!this.referer) {
      this.$router.go(-1);
    }
  }
};
</script>

<style lang="less" scoped>
.referer-detail {
  background-color: @white;
  &-content {
    border-radius: 5px;
  }
  &-bottom {
    padding-top: 10px;
    display: flex;
    align-items: center;
    & > div {
      flex: 1;
      text-align: center;
      &:first-child {
        text-align: start;
      }
      &:last-child {
        text-align: end;
      }
    }
    &-content {
      color: @blue;
      font-size: 14px;
    }
  }
}
</style>
